    vm.proceed = function () {
      if(vm.information.relationship == 'self') {
        vm.informationTemplate.Risks[0].RiskBody.InsuredParty.Name = vm.informationTemplate.Header.Owner.Name
        vm.informationTemplate.Risks[0].RiskBody.InsuredParty.Dob = moment(vm.informationTemplate.Header.Owner.Dob).format("YYYY-MM-DD")
        vm.informationTemplate.Risks[0].RiskBody.InsuredParty.Gender = vm.informationTemplate.Header.Owner.Gender
      } else if(vm.information.relationship == 'relative') {
        vm.informationTemplate.Risks[0].RiskBody.InsuredParty.Name = vm.information.name
        vm.informationTemplate.Risks[0].RiskBody.InsuredParty.Dob = moment(vm.information.birthDate).format("YYYY-MM-DD")
        vm.informationTemplate.Risks[0].RiskBody.InsuredParty.Gender = vm.information.gender
        vm.informationTemplate.Risks[0].RiskBody.InsuredParty.Kinship = vm.information.relationshipType
      }
      PurchaseService.setInformation2(vm.informationTemplate);
      $state.go('purchase-11');
    };

