(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .controller('Claim4Controller', Claim4Controller);

  /** @ngInject */
  function Claim4Controller($state, $stateParams, ClaimService) {
    var vm = this;

    vm.claimBodyTemplate = ClaimService.ClaimBody

    vm.benefit = '';

    vm.benefits = [
      {
        display: 'Death benefit',
        value: 'A1'
      },
      {
        display: 'Permanent disability insurance benefit',
        value: 'A2'
      },
      {
        display: 'Hospitalization insurance benefit',
        value: 'A3'
      },
      {
        display: 'Surgery insurance benefit (during hospitalization)',
        value: 'A4_a'
      },
      {
        display: 'Surgery insurance benefit (other)',
        value: 'A4_b'
      },
      {
        display: 'Personal Liability',
        value: 'B1'
      }
    ];

    vm.btn1 = 'Continue';

    vm.proceed = function() {
      ClaimService.setBenefit(_.find(vm.benefits, function(b)  {
        return b.value === vm.benefit;
      }), $stateParams.id);
      vm.claimBodyTemplate.Header.Contid = ClaimService.policyDetails.Cont.ContID;
      vm.claimBodyTemplate.Header.ClaimItems[0].Benefit = ClaimService.claims[0].benefit.value;
      ClaimService.setClaimBody(vm.claimBodyTemplate)

      $state.go('claim-5');
    };

    function init() {
      if($stateParams.id >= 0) {
        if(ClaimService.claims[parseInt($stateParams.id, 10)]) {
          vm.benefit = ClaimService.claims[parseInt($stateParams.id, 10)].benefit.value;
        } else {
          $state.go('claim-1');
        }
      } else if($stateParams.id !== '-1') {
        $state.go('claim-1');
      }
    }

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('rakutenPaDemo')
    .controller('Claim3Controller', Claim3Controller);

  /** @ngInject */
  function Claim3Controller($state, ClaimService, $http) {
    var vm = this;

    vm.polNo = ClaimService.polNo
    vm.claims = [];

    vm.text1 = 'Personal Accident';
    vm.text2 =  'Policy no. ' + vm.polNo;

    vm.text3 = 'What would you like to claim for?';

    vm.text4 = 'No benefits have been added yet. Click here to add benefits.';

    vm.text5 = '+ Add a benefit';
    vm.btn1 = 'Complete';

    vm.edit = function(id) {
      $state.go('claim-4', {id: id})
    };

    vm.add = function () {
      $state.go('claim-4', {id: -1});
    };

    vm.proceed = function() {
      vm.btn1 ='Loading...'; 
      var attachment = ClaimService.ClaimBody;
      attachment.Header.ClaimItems[0].ClaimedAmount = parseInt(attachment.Header.ClaimItems[0].ClaimedAmount);
      var req = {
        method: 'POST',
        url: 'https://general.sonic.dev.coherent.com.hk/general/v2/claim/initiate',
        data: attachment
      };
      $http(req)
        .then(function (response) {
          ClaimService.setClaimRef(response.data.ClaimRef);
          $state.go('claim-9');
        }, function () {
      });

    }

    function init() {
      if(ClaimService.claims.length > 0) {
        vm.claims = ClaimService.claims;
      }
    }

    init();
  }
})();

